.app-wrapper {
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-areas: "head head"
    "navigation content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    grid-area: content;
    background-color: yellow;
}




